import React from 'react';
import Mytab from './Mytab';

import { Carousel} from 'antd-mobile';

import { SearchBar, WingBlank } from 'antd-mobile';
import { Flex, WhiteSpace } from 'antd-mobile';


class Details extends React.Component {
  state = {
    data: ['1', '2', '3','4','5','6','7','8'],
    imgHeight: 176,
  }

  componentDidMount() {
   //实现轮播图
   // simulate img loading
   setTimeout(() => {
    this.setState({
      // data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI','AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
      data:['2345_image_file_copy_1.jpg','2345_image_file_copy_2.jpg','2345_image_file_copy_3.jpg','2345_image_file_copy_4.jpg','2345_image_file_copy_5.jpg','2345_image_file_copy_6.jpg','2345_image_file_copy_7.jpg','2345_image_file_copy_8.jpg',]
    });
  }, 100)
  }
  
  onChange= (value) => {
    this.setState({ value });
    //跳转页面
    console.log(this.props);

  };
  clear = () => {
    this.setState({ value: '' });
  };
  handleClick = () => {
    this.manualFocusInst.focus();
  }
  changeTo=()=>{
    //实现跳转到其他一个页面显示效果
    this.props.history.push('/content');

  }
  getImage=()=>{
      return (
       <Flex style={{paddingLeft:'15px',paddingRight:'15px',marginTop:"15px"}}>
        <Flex.Item  onClick={this.changeTo} >
        <div><img src="index.png" className="img-responsive"></img></div>
        <p>
          乡村田园自带蕾丝边小帘头抽绳水波单层
        </p>
        <p>￥ 130</p><span>13人已经购买</span>
        </Flex.Item>

        <Flex.Item  onClick={this.changeTo}>
        <div><img src="index.png" className="img-responsive"></img></div>
        <p>
          乡村田园自带蕾丝边小帘头抽绳水波单层
        </p>
        <p>￥ 130</p><span>13人已经购买</span>
        </Flex.Item>

      </Flex>
      )
    }

  render() {
    return (<div style={{heihgt:'20%'}}>
      <SearchBar placeholder="寻找宝贝店铺" ref={ref => this.autoFocusInst = ref} onFocus={()=>{
        this.props.history.push('/search');
      }} />
      
     {/* 轮播图 */}
     <WingBlank>
        <Carousel
          autoplay
          infinite
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                // src={`https://zos.alipayobjects.com/rmsportal/${val}.png`}
                src={`images/${val}`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>

   
    {/* <div>{this.getImage()}</div> */}
    {this.getImage()}
    {this.getImage()}
    {this.getImage()}
    {this.getImage()}
    {this.getImage()}
    {this.getImage()}
    <Mytab/>
    </div>);
  }

}
export default  Details;