import React,{Fragment} from 'react';
import './content.css';
import Mylist from './Mylist';
import { Flex, WhiteSpace } from 'antd-mobile';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap';

class Content extends React.Component{
    constructor(){
        super();
        
    }
    backPage=()=>{
        this.props.history.goBack('/details');
    }
    render(){
        return (
            <Fragment>
                <div className="wap">
                    {/* 实现按钮部分 */}
        <div className="container Imgbox" style={{paddingLeft:'0px',paddingRight:"0px"}}>
        <p className="point" onClick={this.backPage}><span style={{color:"#fff",fontSize:"20px",marginLeft:"5px"}}>{`<`}</span></p>
        <img src='index.png' className="img-responsive"></img>


        </div>
    
        




                <Mylist/>
                </div>

            </Fragment>
         
        )
    }

}


export default Content;