import React from "react";
import 'antd-mobile/dist/antd-mobile.css';
import { TabBar } from 'antd-mobile';
import {Button} from 'antd-mobile';
import {withRouter} from 'react-router-dom';

class Mytab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      //是否选中当前页面，然后修改tabbar为相应的颜色图片
      selectedTab: '/',
    };
  }
 
  render() {
    return (
      <div style={{position: 'fixed',bottom:0, width: '100%', }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
        >
          <TabBar.Item
            title="首页"
            key="home"
        //设置tabBar背景图片
            icon={
                <i className="iconfont icon-icon_home"></i>
            }
            // 点击icon以后的tabbar的样式
            selectedIcon={
                <i className="iconfont icon-xuanzhongshangcheng"></i>

            }
            //判断是点击tabBar，然后跳转到指定的页面  首页是默认显示的
            selected={this.props.location.pathname=== '/'}
            onPress={() => {
              console.log(this.props);
              this.props.history.push('/')

            }}
            data-seed="logId"
          >
        
          </TabBar.Item>
      
          <TabBar.Item
            icon={
             <i className="iconfont icon-gouwuche"></i>
            }
            selectedIcon={
               <i className="iconfont icon-chat-gray"></i>
            }
            title="购物车"
            key="购物车"
            selected={this.props.location.pathname=== '/gowuche'}
            onPress={() => {
              // console.log("购物车页面被选中");
             this.props.history.push('/gowuche');
            }}
            data-seed="logId1"
          >
            <Button type="primary" onClick={(props)=>{
                //history.push方法实现跳转
                console.log("doc:",this.props);
                this.props.history.push('/doc');
            }}>click</Button>
            {/* {this.renderContent('Koubei')} */}
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className="iconfont icon-list"></i>
            
            }
            selectedIcon={
              <i className="iconfont icon-Listmenuoptions"></i>
           
            }
            title="订单列表"
            key="订单列表"
       
            selected={this.props.location.pathname=== '/orderlist'}
            onPress={() => {
            this.props.history.push('/orderlist');
            }}
          >
      
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className="iconfont icon-lianxiren1"></i>
              
            }
            selectedIcon={
              <i className="iconfont icon-wodedangxuan"></i>
              
            }
            title="我的淘宝"
            key="我的淘宝"
            selected={this.props.location.pathname=== '/owner'}
            onPress={() => {
            this.props.history.push('/owner');
            }}
          >
      
          </TabBar.Item>
          {/* 更多 */}
          <TabBar.Item
            icon={
              <i className="iconfont icon-sangedian"></i>
              
            }
            selectedIcon={
              <i className="iconfont icon- sangedian"></i>
              
            }
            title="更多"
            key="more"
            selected={this.props.location.pathname=== '/more'}
            onPress={() => {
            this.props.history.push('/more');
            }}
          >
        
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}


export default withRouter(Mytab);



